.orange {
  color : orange;
}

.profile {
  position: relative;
  width: 300px;
  text-align: center;
  padding: 1rem;
  background-color: #EBEBEB;
  border-radius: 20px;
  box-shadow: 13px 11px 10px -1px rgba(0,0,0,0.43);
  -webkit-box-shadow: 13px 11px 10px -1px rgba(0,0,0,0.43);
  margin-bottom: 1rem;
  -moz-box-shadow: 13px 11px 10px -1px rgba(0,0,0,0.43);
}

.photo {
  width: 200px;
  height: 200px;
  border-radius: 100%;
}

.avatar {
  position: relative;
  width: 200px;
  height: 200px;
  margin: auto;
}

.new {
  position: absolute;
  left: 70%;
  top: 10%;
  background-color: aquamarine;
  padding: 0.2rem 0.5rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-weight: bold;

}